.header {
  background-color: var(--second-bg);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 6;
  padding-top: 8px;
  padding-bottom: 8px;

  @media screen and (min-width: $tablet) {
    padding-top: 11px;
    padding-bottom: 11px;
  }

  @media screen and (min-width: $desktop) {
    padding-top: 16px;
    padding-bottom: 16px;
  }
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media screen and (min-width: $tablet) {
    padding-right: 32px;
    padding-left: 32px;
  }
  @media screen and (min-width: $desktop) {
    padding-right: 16px;
    padding-left: 16px;
  }
}

.header__logo {
  display: flex;
  align-items: center;
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: var(--title-text);
}

.header__img {
  width: 30px;
  margin-right: 18px;
  @media screen and (min-width: $tablet){
    width: 40px;
  }
}

.header__button {
  @media screen and (max-width: 767.5px) {
    display: none;
  }

  @media screen and (min-width: $tablet) {
    align-items: center;
    margin-left: 32px;
  }

  @media screen and (min-width: $desktop) {
    margin-left: 0;
  }
}
.mobile-menu {
  @media screen and (min-width: $desktop) {
    display: none;
  }
}

.navigation {
  @media screen and (max-width: 1279.5px) {
    display: none;
  }
}

.site-nav {
  display: flex;
  align-items: center;

  &__item {
    margin-right: 35px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    padding-top: 20px;
    padding-bottom: 20px;
    color: var(--white-text);
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:focus,
    &:hover {
      color: var(--title-text);
    }

    &--current {
      color: var(--title-text);
    }
  }
}

.button {
  font-family: inherit;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  background-color: var(--white-text);
  color: var(--title-text);
  stroke: var(--title-text);
  cursor: pointer;
  border-radius: 22px;
  border: transparent;
  min-width: 160px;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:focus,
  &:hover {
    background-color: var(--hover-bg);
  }
}
